<template>
  <div id="btn">
    <div class="btn_1" v-show="num>=1" @click="minus">-</div>
    <div class="text">
      <input type="text" v-show="num<0" v-model="num" style="width:20px" />
      {{num>=1?num:""}}
    </div>
    <div class="btn_2" @click="add">+</div>
  </div>
</template>

<script>
export default {
  props: ["value"],
  computed: {
    num: {
      get() {
        return this.value;
      },
      set(v) {
        this.$emit("value", v);
      }
    }
  },
  methods: {
    add() {
      this.num++;
    },
    minus() {
      if (this.num <= 0) {
        return;
      }
      this.num--;
    }
  }
};
</script>

<style>
#btn .btn_1,
#btn .btn_2 {
  width: 0.4rem;
  height: 0.4rem;
  border: 0.04rem solid #028fff;
  line-height: 0.4rem;
  border-radius: 50%;
  font-size: 0.36rem;
  color: #028fff;
}
#btn .btn_2 {
  color: #fff;
  background: #028fff;
}

#btn {
  display: flex;
  text-align: center;
}

#btn .text {
  flex: 1;
  line-height: 0.4rem;
}
</style>