import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    p: 0,

    my_arr: [],
  },
  mutations: {
    addItem(state, item) {
      state.my_arr = item
    }
  },
  actions: {

  },
  modules: {

  },
  getters: {
    pic_s(state) {
      var t = 0
      state.my_arr.forEach(item => {
        t += (item.jg * item.num)
      })
      return t
    }
  }
})
