<template>
  <div>
    <mt-header title="包子铺">
      <router-link to="/" slot="left">返回</router-link>
    </mt-header>
    <div id="home">
      <div class="tab">
        <ul>
          <li
            v-for="(i,$i) in list"
            :key="$i"
            @click="index=$i"
            :style="{color:$i==index?'blue':''}"
          >{{i.title}}</li>
        </ul>
      </div>
      <div style="flex:1">
        <list_s :arr="list" :index="index" @index="index=$event"></list_s>
      </div>
      <div class="h_fot" v-show="pic_s>0">
        <div
          style="font-size:.36rem;color:#fff;flex:1;text-align: left;
  padding: 0.15rem 0  0.15rem  1.3rem;"
        >
          ￥{{pic_s}}
          <span>25元起送</span>
        </div>
        <div class="btn_s" @click="btn">去结算</div>
      </div>
    </div>
  </div>
</template>

<script>
import list_s from "../components/list_s";
import { mapGetters } from "vuex";
export default {
  name: "home",
  components: { list_s },
  data() {
    return {
      list: "",
      index: 2
    };
  },
  created() {
    this.$http.get("json.json").then(data => {
      this.list = data.data.arr;
    });
  },
  computed: {
    ...mapGetters(["pic_s"])
  },
  methods: {
    btn() {
      if (this.pic_s >= 25) {
        location.href = "#/about";
      } else {
        alert("25元起送");
      }
    }
  }
};
</script>

<style>
#home {
  display: flex;
  width: 100%;
  margin-bottom: 1rem;
}
#home .tab {
  width: 1.3rem;
  margin-right: 0.15rem;
  background: #f8f8f8;
}
#home .tab li {
  padding: 0.3rem 0;
}

.h_fot {
  position: fixed;
  min-height: 0.6rem;
  width: 100%;
  background: #504f55;
  bottom: 0;
  display: flex;
}
.h_fot .btn_s {
  padding: 0.15rem 0;
  font-size: 0.24rem;
  line-height: 0.45rem;
  width: 1.8rem;
  background: #39c972;
  color: #fff;
}
</style>