<template>
  <div>
    <div v-for="(i,$index) in list_arr" v-show="type==$index" :key="$index">
      <div style="min-height:.4rem;line-height:.4rem;font-size:.2rem;text-align:left">{{i.title}}</div>
      <div id="list_s" v-for="(j,$j) in  i.arr " :key="$j">
        <div class="l_1">
          <img :src="j.url" alt width="100%" height="100%" />
        </div>
        <div class="l_2">
          <div class="l_h3">{{j.title}}</div>
          <p>{{j.ice}}</p>
          <div class="p_n">
            <div class="pic">￥{{j.jg}}</div>
            <div class="num">
              <btn :value="j.num" @value="btn_s(j,$event)"></btn>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import btn from "./btn";
import { mapState } from "vuex";
export default {
  components: {
    btn
  },
  props: ["arr", "index"],
  data() {
    return {
      list: []
    };
  },
  computed: {
    list_arr() {
      return this.arr;
    },
    type: {
      get() {
        return this.index;
      },
      set(v) {
        this.$emit("index", v);
      }
    },
    ...mapState(["my_arr"])
  },
  watch: {
    list_arr: {
      handler(a) {
        this.list = [];
        a.forEach(el => {
          el.arr.forEach(ele => {
            if (ele.num > 0) this.list.push(ele);
          });
        });
        this.$store.commit("addItem", this.list);
      },
      deep: true
    }
  },
  created() {},
  methods: {
    btn_s(item, ev) {
      item.num = ev;
    }
  }
};
</script>

<style>
#list_s {
  display: flex;
  text-align: left;
  padding-bottom: 0.35rem;
}
#list_s .l_1 {
  width: 1.64rem;
  min-height: 1.64rem;
}
#list_s .l_2 {
  padding-left: 0.15rem;
  flex: 1;
}

#list_s .l_h3 {
  font-size: 0.24rem;
  font-weight: bold;
  line-height: 0.3rem;
}
#list_s p {
  font-size: 0.16rem;
  color: #999;
  line-height: 0.36rem;
  padding-bottom: 0.3rem;
}
#list_s .p_n {
  overflow: hidden;
  font-size: 0.2rem;
}
#list_s .pic {
  color: #d89680;
  width: 50%;
  float: left;
  font-size: 0.24rem;
}
#list_s .num {
  width: 50%;
  float: right;
}
</style>